@import '../../../../shared/view.scss';

.fade-in-slow {
  overflow: hidden;
  width: 30em;
  animation: in 4s steps(60, end); 
  background: radial-gradient(ellipse at bottom, #1B2735 0%, #090A0F 100%);

}

@keyframes in{ 
    from { width: 0; } 
  } 

  .fade-out-slow {
      overflow: hidden;
      width: 4.5em;
      animation: out 4s steps(8, end);
     background: radial-gradient(ellipse at bottom, #1B2735 0%, #090A0F 100%);
    height: 100vh;
  }

  @keyframes out{ 
    from { width: 4.5; }
    to { width: 0; } 
  } 

.full-content {
  width: 100%;
  height: 100vh;
  display: table;
  overflow: hidden;
}

.words {
  display: table-cell;
  vertical-align: middle;
  line-height: 5em;

}

@media (max-width: $maxTabletSize) { 
  .words {
    line-height: 2em;
    font-size: 1.5em;
  }
}


.header-class {
  background: black;
  // background: #7F7FD5;  /* fallback for old browsers */
  // background: -webkit-linear-gradient(to right, #91EAE4, #86A8E7, #7F7FD5);  /* Chrome 10-25, Safari 5.1-6 */
  // background: linear-gradient(to right, #91EAE4, #86A8E7, #7F7FD5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

@keyframes animStar {
  from { transform: rotate(0deg) translateX(80px) rotate(0deg); }
  to   { transform: rotate(360deg) translateX(80px) rotate(-360deg); }
}

@function multiple-box-shadow ($n) {
  $value: '#{random(3000)}px #{random(2395)}px #FFF';
  @for $i from 2 through $n {
    $value: '#{$value} , #{random(3000)}px #{random(1000)}px #FFF';
  }
  @return unquote($value);
}


#stars {
  width: 1.5px;
  height: 1.5px;
  background: transparent;
  box-shadow: multiple-box-shadow(500);
  animation: animStar 30s linear infinite;
}
